
    .MainCard {
        height: 27rem;
        width: 22rem;
        background-color: rgb(238, 238, 238);
        color: black;
        border-radius: 3px;
    
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
    
        transition: all 0.2s;
    
        text-decoration: none;
        color: black;

        &:hover {
            cursor: pointer;
            transform: translateY(-0.5rem) scale(1.05);
        }
        
        &Text {
            margin-top: -2rem;
            font-size: 2rem;
        }
    }


@media (max-width: 1920px) {
    .MainCard {
        height: 25rem;
        width: 21rem;

        &Text {
            font-size: 1.8rem; 
        }
    }
}

@media (max-width: 1600px) {
    .MainCard {
        height: 24rem;
        width: 20rem;

        &Text {
            font-size: 1.8rem;
        }
    }
}

@media (max-width: 1370px) and (min-height: 1000px) {
    .MainCard {
        height: 30rem;
        width: 25rem;

        &Text {
            font-size: 2.4rem;
        }
    }
}

@media (max-width: 1030px) and (min-height: 1360px) {
    .MainCard {
        height: 30rem;
        width: 25rem;

        &Text {
            font-size: 2rem; 
        }
    }

}

@media (max-width: 820px) and (max-height: 445px) {
    .MainCard {
        height: 22rem;
        width: 18rem;
    }
}

@media (max-width: 768px) {
    .MainCard {
        height: 26rem;
        width: 24rem;

        &Text {
            font-size: 1.9rem;  
        }
    }
}

@media (max-width: 600px) {
    .MainCard {
        height: 24rem;
        width: 22rem;
    }
}

@media (max-width: 450px) {
    .MainCard {
        height: 23rem;
        width: 21rem;
    }
}

@media (max-width: 425px) {
    .MainCard {
        height: 27rem;
        width: 23rem;
    }
}