@import '../../../../scss/mixins';

.OrderItem {
    display: flex;
    background-color: black;
    color: rgb(238, 238, 238);
    height: 10rem;
    width: 35rem;

    &Content {
        flex: 0 0 70%;
        height: 100%;
        padding: 1rem;
    
        display: flex;
        flex-direction: column;
        justify-content: space-around;
    }

    &ButtonContainer {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: auto;
    }

    &Button {
        background-color: black;
        border: none;
        opacity: 0.5;
        height: 100%;
        width: 100%;
    
        transition: all .1s linear;

        &:hover {
            cursor: pointer;
            opacity: 1; 
        }
    }

    &List {
        list-style: none;

        &Item {
            font-size: 1.3rem;
        }
    }
}

