.BrandLogoItem {
  width: 22rem;
  height: 22rem;
  border-radius: 3px;
  background-color: white;
  opacity: 1;
  filter: grayscale(100%);
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all .2s; }
  .BrandLogoItem:hover {
    filter: none;
    transform: scale(1.1);
    cursor: pointer; }
  .BrandLogoItemImg {
    opacity: 1;
    filter: none; }

.standard {
  width: 15rem;
  height: 15rem; }

.small {
  width: 13.5rem;
  height: 13.5rem; }

